require("jquery.easing")
require('bootstrap-loader');
require("font-awesome-loader");
require("../css/style.css");


$(function() {
    var offset = 32;
    $('#main-nav li a').click(function(e) {
        e.preventDefault();
        var href = $(this).attr('href');

        $(href)[0].scrollIntoView();
        scrollBy(0, offset);
    });

    $(window).scroll(function() {
        var $navbar = $(".navbar");
        if ($navbar.offset().top > 50) {
            $navbar.addClass("small-header");
        } else {
            $navbar.removeClass("small-header");
        }
    });

    window.sr = ScrollReveal();
    sr.reveal('#web-app-container', { origin: 'right', delay: 300, distance: '400px' });
    sr.reveal('#consulting-container', { origin: 'left', delay: 300, distance: '400px' });
    sr.reveal('#company-container', { delay: 300 });
});



